.hex-image-wrapper,
.hex-image {
  clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
}
.hex-image-wrapper {
  background: linear-gradient(135deg, #a354b1 0%, #2fd7ff 100%);
}
.hex-image {
  width: 100%;
  height: 100%;
  transform: scale(0.99);
  top: 2px;
  position: relative;
  transform-origin: center center;
}
