.Overlay {
  position: fixed;
  width: 100%;
  height: calc(100% - 70px);
  top: 70px;
  z-index: 9;
  display: flex;
  align-items: center;
  transition: all 350ms ease-in-out;
  // opacity: 0;
  // pointer-events: none;
  // &.--active{
  //     opacity: 1;
  //     pointer-events: all;
  // }
  .ValueCounter {
    margin-top: 10px;
  }
  .Overlay__cont {
    width: 600px;
    margin: 0 auto;
    color: #fff;
    // height: 300px;
    display: flex;
  }
}

