@import 'node_modules/jeet/jeet';

$ovr-purple: #1d0333;
$ovr-light-purple: #c81d5e;
$ovr-sideral-grey: #282d32;
$ovr-grey: #857f7c;
$ovr-light-grey: #f3f3f5;
$ovr-aqua-marine: #c9d2d3;
$ovr-orange: #ec663c;
$ovr-yellow: #f9b426;
$ovr-green: #49b6ae;
$ovr-light-blue: #2fd7ff;
$ovr-red: #c32438;

$breakpoints: (
  phone-down: 319px,
  phone: 320px,
  mobile-down: 540px,
  mobile: 541px,
  tab-down: 767px,
  desk-down: 1023px,
  desk: 1024px,
  large: 1200px,
  x-large: 1600px,
) !default;

// Managing container max-width
@function bk($bk: 'mobile') {
  @if not map-has-key($breakpoints, $bk) {
    @warn "No breakpoint value found in $breakpoints map for '#{$bk}'. Property omitted.";
  }
  @return map-get($breakpoints, $bk);
}

$font-family-body: 'Work Sans', sans-serif;
body {
  font-family: $font-family-body;
  font-weight: 300;
  font-size: 16px;
  // background-color: $ovr-light-grey;
  background-color: #fff;
}

.o-line,
.o-line--in {
  display: block;
  width: 100%;
  clear: both;
}
.o-line--in {
  display: inline-block;
}

.o-half {
  @include column(1/2);
}
.c-one-third {
  @include column(1/3);
}
.c-two-third {
  @include column(2/3);
}
.o-fourth {
  @include column(1/4);
}
.o-third {
  @include column(1/3, $cycle: 3);
}
.o-fifth {
  @include column(1/5);
}
.Land__heading__1 {
  @include column(2/5);
}
.Land__heading__2 {
  @include column(3/5);
}

.o-container {
  margin: 0 auto;
  width: calc(100% - 140px);
  @media only screen and (max-width: bk('desk')) {
    width: 90%;
  }
  @media only screen and (min-width: bk('x-large')) {
    width: 80%;
  }
}

.App {
  padding-top: 110px;
}

.o-section-title {
  margin-top: 30px;
  color: $ovr-sideral-grey;
}
