@media screen and (max-width: 420px) {
  .Signup .o-box {
    max-width: 450px;
    padding: 25px;
  }
  .Footer {
    width: calc(100% - 20px);
    .o-fourth {
      @include column(1/2, $cycle: 2);
      &:nth-child(2) {
        margin-top: 5px;
      }
      &:nth-child(3),
      &:nth-child(4) {
        margin-top: 20px;
      }
    }
  }
  .Social_buttons {
    display: flex;
    justify-content: center;
  }
  .Signup .Signup__msg {
    padding: 4px;
  }
  .p-body .o-fourth {
    display: none;
  }
  .profile .profile-content,
  .profile .p-body {
    width: 100%;
  }
  .p-wallet-address {
    font-size: 12px;
  }
  .profile .p-section .p-section-content {
    width: 100%;
  }
  .Profile__link span,
  .Navbar__link_container .NavBar__link {
    display: none;
  }
  .Navbar__link_container .Language__link {
    display: inline;
  }

  .--install-imtoken {
    margin-left: 0px;
    margin-top: 10px !important;
  }
}

.ignore-this {
  font-size: 8px;
}

@media screen and (max-width: 700px) {
  .p-header-datetime {
    font-size: 12px;
  }
  .activity {
    padding-top: 4%;
    min-height: 60vh;

    .o-container {
      width: 85%;
    }
    .p-header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .p-header-title {
        font-family: Arial, 'Helvetica Neue', Helvetica, sans-serif;
        font-size: 25px;
      }
    }
    .sub-title {
      font-size: 14px;
      color: $ovr-grey;
    }
  }

  .ActivityTile {
    padding-right: 0px;
  }

  .NotificationCenter {
    left: 5%;
    top: 80px;
    width: 90%;
    z-index: 99;
    border: 1px #ccc solid;
  }
}

.o-select-multiple-land-link {
  padding: 0px 0px;
  text-decoration: underline;
  color: $ovr-sideral-grey;
  margin-left: 15px;
  font-weight: 200;
  line-height: 30px;
  margin-right: 20px;
  font-size: 15px;
  float: right;
  display: inline-block;
}
.--w-link {
  display: inline-block;
  width: 100%;
  span {
    float: left;
  }
}
