.Notification {
  font-size: 14px;
  position: relative;
  padding: 18px 25px;
  border-bottom: 1px solid rgba(43, 48, 52, 0.1);
  line-height: 20px;
  text-align: left;
  cursor: pointer;
  .MuiButtonBase-root {
    opacity: 0;
  }
  .TimeCounter {
    color: $ovr-grey;
  }
  &:hover {
    background-color: rgba($ovr-light-grey, 0.4);
    .Notification__content {
      // text-decoration: underline;
    }
    .MuiButtonBase-root {
      opacity: 1;
    }
  }
  &.--new {
    &:after {
      background-color: #7521c8;
      width: 6px;
      height: 6px;
      border-radius: 10px;
      content: ' ';
      display: block;
      position: absolute;
      top: 26px;
    }
  }
  button {
    position: absolute;
    right: 14px;
    top: 14px;
  }
}
.Notification__body {
  padding-left: 18px;
}
.Notification__content {
  clear: both;
  span {
    font-weight: 400;
  }
}
.Notification__time {
  clear: both;
}

.MuiPaper-elevation8 {
  box-shadow: -10px 10px 20px 0 rgba(30, 30, 30, 0.15) !important;
}
.MuiButtonBase-root {
  font-size: 14px !important;
}

.notification-danger {
  background-color: $ovr-red !important;
}
.notification-warning {
  border-left: #1d0333 !important;
  background: linear-gradient(138deg, #1d0333, #805e9d) !important;
}
