.LandCard {
  background: #ffffff;
  display: block;
  box-shadow: -6px 7px 21px 0 rgba(30, 30, 30, 0.05);
  @include column(1/4, $cycle: 4);
  border-radius: 5px;
  overflow: hidden;
  float: left;
  transition: 350ms all cubic-bezier(0.19, 1, 0.22, 1);
  background-position: center center;
  background-size: 100%;
  &:hover {
    box-shadow: 0px 0px 20px rgba(30, 30, 30, 0.05);
    transform: scale(0.97);
  }
  .LandCard__body {
    float: left;
    padding: 20px;
  }
  .TimeCounter {
    margin-top: 10px;
  }
  margin-bottom: 40px;
  .ola0 {
    fill: none;
    stroke: #56c0e6;
  }

  position: relative;
}

.LandCard__header {
  height: 120px;
  width: 100%;
  background-position: center center;
  background-size: 120%;
}
.LandCard__cont {
  display: flow-root;
}

.o-land-list {
  .o-container {
    flex-flow: row wrap;
  }
}

