@media screen and (max-width: 1000px) {
  .LandCard {
    @include column(1/2, $cycle: 2);
  }
}

@media screen and (max-width: 780px) {
  .LandCard {
    @include column(1/1, $cycle: 1);
  }
}

.LandCard__art_holder {
  width: 60px;
  height: 60px;
  position: relative;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  overflow: hidden;
  svg {
    position: absolute;
    width: 60px;
    height: 60px;
    z-index: 1;
  }
  canvas {
    width: 100%;
    height: 100%;
    top: 0px;
    opacity: 1;
    left: 0px;
    position: relative;
    transform: scale(0.7);
  }
}
