.Signup {
  text-align: center;
  padding-top: 50px;
  background-image: linear-gradient(90deg, #5e509d 0%, #724a93 100%);
  min-height: calc(100vh - 300px);
  padding-bottom: 50px;
  .MuiCircularProgress-root {
    width: 25px !important;
    height: 25px !important;
    top: 8px;
    position: relative;
    left: 15px;
    svg {
      color: rebeccapurple;
    }
  }
  .o-box {
    background: #fff;
    border-radius: 5px;
    margin: 0 auto;
    max-width: 450px;
    padding: 45px;
    .Logo {
      max-width: 100px;
      height: auto;
      margin-bottom: 20px;
    }
    h1,
    h2 {
      margin-top: 0px;
      color: $ovr_purple;
    }
    .HexButton {
      margin-top: 25px;
    }
  }
  .--left {
    text-align: left;
    white-space: pre-line;
  }
  .o-list {
    line-height: 20px;
    svg {
      margin-left: 5px;
      max-width: 18px;
      display: inline-block;
      position: relative;
      top: 7px;
    }
  }
  .CheckCircleSharpIcon {
    fill: #00c8a1;
  }
  .CancelIcon {
    fill: #dc6a5b;
  }
  .Signup__msg {
    &.--positive {
      background: rgba(#00c8a1, 0.1);
      border: 1px solid #00c8a1;
    }
    &.--negative {
      background: rgba(#dc6a5b, 0.1);
      border: 1px solid #dc6a5b;
    }
    border-radius: 5px;
    padding: 10px 25px;
    margin-top: 20px;
    font-weight: 300;
  }
}
.Signup__section {
  font-size: 16px;
  margin-top: 5px;
  margin: 0 auto;
  max-width: 350px;
  clear: both;
  display: inline-block;
  width: 100%;
  &.--small {
    margin-top: 10px;
    font-size: 10px;
    color: $ovr-sideral-grey;
  }
}
.Signup__form-container {
  margin-top: 20px;
}
.Signup__input {
  margin-top: 10px;
  text-align: center;
  .HexButton {
    margin-top: 20px;
  }
  &:first-child {
    margin-top: 0px;
  }
}
.Signup__signup,
.Signup__forgot {
  margin-top: 30px;
  font-size: 12px;
  font-weight: 400;
  color: $ovr-grey;
  a {
    color: $ovr-grey;
  }
}
.Signup__forgot {
  margin-top: 20px;
}

.Signup__footer {
  margin-top: 50px;
  a {
    color: #000;
    text-decoration: none;
    font-weight: 400;
  }
}
.Signup__email_textfield {
  width: 80%;
}

.Signup__userpass_textfield {
  width: 60%;
}
.Signup__pass_cont {
  margin-top: 15px;
}
.MuiSvgIcon-root.CheckCircleIcon {
  height: 80px;
  width: 80px;
  fill: #00c8a1;
  stroke: #00c8a1;
  margin-bottom: 20px;
}

.kep-login-facebook {
  display: inline-block;
  border: none;
  border-radius: 20px;
  background: #4267b2;
  color: #fff;
  white-space: nowrap;
  min-height: 38px;
  font-size: 14px;
  padding: 0px 15px;
  margin-left: 20px;
  line-height: 38px;
  &:hover,
  &:active {
    background-color: #3c5a98;
  }
  .fa {
    margin-right: 15px;
    padding-top: 6px;
    padding-bottom: 5px;
    font-size: 17px;
  }
}
.Social__section button {
  font-size: 14px;
  padding: 0px 15px !important;
  border-radius: 20px !important;
  float: left;

  div {
    padding: 0px !important;
  }
  span {
    padding: 9px 10px 9px 0px;
  }
}

.Social__subtitle {
  margin-bottom: 30px;
  margin-top: 15px;
}

.Social__or {
  margin-top: 20px;
  margin-bottom: 0px;
  clear: both;
  width: 100%;
  display: inline-block;
}

.Social__buttons {
  display: inline-block;
}

.--tutorial .Signup__section {
  font-size: 14px;
}
