.LandName {
  color: $ovr-sideral-grey;
  font-weight: 200;
  line-height: 1;
  .TextCounter {
    float: left;
    margin-right: 40px;
    font-size: 2.5em;
  }
}
.LandName__name {
  font-size: 16px;
  position: relative;
