.LandName__location {
  font-weight: 100;
  margin-top: 8px;
  font-size: 14px;
  max-height: 14px;
  position: relative;
  line-height: 16px;
}

.LandName__name {
  width: 100%;
  height: 17px;
  font-weight: 400;
  overflow: hidden;
  position: relative;
}
.LandName__name span {
  position: absolute;
  white-space: nowrap;
  transform: translateX(0);
  transition: 1s;
}
.LandName__name:hover {
  &:after {
    opacity: 0.8;
  }
  span {
    transform: translateX(calc(150px - 100%));
  }
}
