.profile {
  min-height: 60vh;

  .Help {
    font-size: 14px;
    color: $ovr-grey;
    margin-left: 5px;
    line-height: 16px;
    position: relative;
    top: 2px;
  }
  .c-dialog {
    padding-top: 50px;
  }
  .MuiInput-root {
    max-width: 70%;
  }
  .MuiCircularProgress-root {
    width: 26px !important;
    height: 26px !important;
    margin-top: 20px;
    margin-right: 50px;
    svg {
      color: $ovr-grey;
    }
  }
  .identicon {
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }
  .profile-image {
    width: 80%;
    height: auto;
    margin: 0 auto;
  }
  .p-body {
    width: 80%;
    margin: 0 auto;
    padding-top: 20px;
    padding-bottom: 80px;
    display: flex;
  }
  .profile-content {
    @include column(3/4);
  }
  .p-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: $ovr-grey;

    .p-header-title {
      font-family: Arial, 'Helvetica Neue', Helvetica, sans-serif;
    }
  }
  .p-profile-form {
    .HexButton {
      margin-top: 15px;
    }
  }
  .--email-badge {
    margin-top: 13px;
  }
  .Signup__nationality_holder {
    width: 80%;
    .MuiInput-root {
      width: 100%;
    }
    label {
      font-size: 12px;
      margin-bottom: 2px;
    }
  }
  .p-section {
    .--m-t {
      margin-top: 50px;
    }
    .c-status-badge {
      text-transform: uppercase;
    }
    .p-section-title {
      color: $ovr-grey;
      font-family: $font-family-body;
      margin: 0 0 1rem 0;
    }
    .p-section-content {
      display: inline-block;
      width: 50%;
      .--resend-email {
        float: left;
        left: 60%;
        bottom: 0px;
        position: absolute;
        svg {
          font-size: 20px;
        }
      }
    }
    .p-tiny-message {
      margin-top: 15px;
      font-size: 14px;
      max-width: 450px;
    }
    .p-content-title {
      margin: 0 0 0.5rem 0;
    }
    .p-small-title {
      font-weight: bold;
    }
    .p-balance {
      padding: 1rem 0;
    }
    .p-balance-value {
      display: flex;
      align-items: center;
      justify-content: space-between;
      position: relative;
    }
    // &:first-child .p-section-title {
    // 	margin-top: 0;
    // }
    margin-bottom: 3rem;
  }
}

.p-tiny-message {
  margin-top: 15px;
  font-size: 14px;
}
